body {
  background-color: #0079BF;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  color: #000;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  box-sizing: border-box;
}

header, footer {
  color: #fff;
}
.header-account__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-account__btn {
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  margin-right: 6.8px;
  -webkit-transform: scale(1);
          transform: scale(1);
  position: relative;
  transition: -webkit-transform 0.1s linear;
  transition: transform 0.1s linear;
  transition: transform 0.1s linear, -webkit-transform 0.1s linear;
}

.header-account__btn::after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  border-radius: inherit;
}

.header-account__btn:hover {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.header-account__btn:hover::after {
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.5) inset;
  transition: box-shadow 0.1s linear;
}

.header-account__btn:focus-visible {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
  outline: 2px solid #fff;
  outline-offset: 4px;
}

.header-account__btn:active {
  -webkit-transform: scale(0.90);
          transform: scale(0.90);
}

.header-account__btn:active::after {
  box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.5) inset;
  transition: box-shadow 0.1s linear;
}

.header-account__avatar {
  width: 100%;
  height: 100%;
}

.header-account__arrow {
  width: 12px;
  height: 7.5px;
  background-image: url(/static/media/arrow.82509b24.svg);
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
  transition: -webkit-transform 0.2s linear;
  transition: transform 0.2s linear;
  transition: transform 0.2s linear, -webkit-transform 0.2s linear;
}

.header-account__arrow.is-active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.user-avatar {
}

.user-menu {
  display: flex;
  width: 12px;
  height: 7.5px;
  background-color: transparent;
}
.user-menu__icon {
}

.dropdown-menu {
  position: absolute;
  top: 70px;
  right: 9px;
  min-width: 134px;
  display: flex;
  background-color: #fff;
  border-radius: 5px;
  padding: 10px 7px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s linear, opacity 0.2s linear;
}

.dropdown-menu.is-active {
  visibility: visible;
  opacity: 1;
}

.dropdown-menu::before {
  content: '';
  width: 11px;
  height: 11px;
  display: block;
  background-color: #fff;
  position: absolute;
  top: -6px;
  right: 45px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.dropdown-menu__list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  list-style: none;
  margin: 0;
  padding: 0;
  grid-gap: 10px;
  gap: 10px;
}

.dropdown-menu__item {
}

.dropdown-menu__link {
  color: black;
  font-size: 16px;
  line-height: 21px;
  text-decoration: none;
}

.dropdown-menu__link:hover {
  text-decoration: underline;
}

.dropdown-menu__link:focus-visible {
  color: #0067a3;
  text-decoration: underline;
  outline: none;
}

.dropdown-menu__link:active {
  color: #0067a3;
  text-decoration: none;
}

@media (max-width: 500px) {
  .dropdown-menu {
    top: 65px;
  }

  .dropdown-menu::before {
    right: 40px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  background-color: #0067a3;
  padding: 0 20px;
}

.header__title {
  font-size: 28px;
  line-height: 33px;
}

@media (max-width: 500px) {
  .header {
    background-color: #0079BF;
    padding: 14px 15px 0px;
  }

  .header__title {
    font-size: 0;
  }
}

.main {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  grid-gap: 24px;
  gap: 24px;
  background-color: #0079BF;
  padding: 20px 20px 32px;
  height: calc(100vh - 186px);
  overflow: auto;
}

@media (max-width: 500px) {
  .main {
    height: 100%;
    padding: 90px 20px 180px;
  }
}
.card {
  background-color: #ebecf0;
  padding: 12px 12px 8px;
  border-radius: 10px;
  width: 258px;
  margin-bottom: auto;
}

.card__title {
  font-weight: inherit;
  font-size: inherit;
  margin: 0 0 20px;
}

.card__list {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 490px;
  overflow: auto;
}

.card__text {
  margin-top: 0;
}

.card__item, .card__input, .card__select {
  background-color: #fff;
  padding: 9px 7px;
  margin-bottom: 15px;
  border-radius: 5px;
  border: none;
}

.card__link {
  color: inherit;
  text-decoration: none;
}

.card__link:hover {
  text-decoration: underline;
}

.card__link:focus-visible {
  color: #0067a3;
  text-decoration: underline;
  outline: none;
}

.card__link:active {
  color: #0067a3;
  text-decoration: none;
}

.card__subtitle {
  word-wrap: break-word;
  font-weight: inherit;
  font-size: inherit;
  line-height: 21px;
  margin: 0;
}

.card__btn--add {
  display: none;
  align-items: center;
  color: #5e6c84;
  background: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 5px;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: background-color 0.1s linear, outline 0.1s linear, -webkit-transform 0.1s linear;
  transition: background-color 0.1s linear, transform 0.1s linear,
  outline 0.1s linear;
  transition: background-color 0.1s linear, transform 0.1s linear,
  outline 0.1s linear, -webkit-transform 0.1s linear;
}

.card__btn--add:hover, .card__btn--add:focus-visible, .card__btn--submit:hover {
  background-color: #fff;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.card__btn--add:focus-visible, .card__input:focus-visible, .card__btn--submit:focus-visible, .card__select:focus-visible {
  outline: 2px solid #0067a3;
  outline-offset: 2px;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.card__btn--add:active, .card__btn--submit:active {
  -webkit-transform: scale(0.90);
          transform: scale(0.90);
}

.card__btn--add.is-active {
  display: flex;
}

.btn-icon {
  margin-right: 4px;
}

.card__form {
  display: flex;
  flex-direction: column;
}

.card__input {
  transition: outline 0.1s linear,
  -webkit-transform 0.1s linear;
  transition: transform 0.1s linear,
  outline 0.1s linear;
  transition: transform 0.1s linear,
  outline 0.1s linear,
  -webkit-transform 0.1s linear;
}

.card__btn--submit {
  align-self: self-start;
  color: #fff;
  font-size: 18px;
  line-height: 21px;
  background-color: #0067A3;
  padding: 4px 21px;
  border: none;
  cursor: pointer;
  transition: background-color 0.1s linear, outline 0.1s linear, -webkit-transform 0.1s linear;
  transition: background-color 0.1s linear, transform 0.1s linear,
  outline 0.1s linear;
  transition: background-color 0.1s linear, transform 0.1s linear,
  outline 0.1s linear, -webkit-transform 0.1s linear;
}

.card__btn--submit:hover {
  background-color: #0079bf;
  padding-top: 2px;
  padding-bottom: 6px;
}

.card__select {
  width: 100%;
  cursor: pointer;
  transition: outline 0.1s linear,
  -webkit-transform 0.1s linear;
  transition: transform 0.1s linear,
  outline 0.1s linear;
  transition: transform 0.1s linear,
  outline 0.1s linear,
  -webkit-transform 0.1s linear;
}
.card__option {
  width: 100%;
  cursor: pointer;
}

@media (max-width: 500px) {
  .card__list {
    max-height: 150px;
  }
}
.modal {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 77px;
  left: 0;
}

.modal__wrap {
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 1180px;
  max-height: 548px;
  margin-top: 80px;
  background-color: #fff;
  padding: 20px 30px;
  margin: 0 20px;
  border-radius: 5px;
}

.modal__title {
  margin-bottom: 35px;
}

.modal__text {
  width: 80%;
  height: 80%;
  border: 2px solid transparent;
  transition: border 0.2s linear;
}

.modal__text:hover {
  cursor: pointer;
}

.modal__text:focus {
  outline: none;
  border-color: #0067a3;
  cursor: auto;
}

.modal__btn {
  position: absolute;
  top: 17px;
  right: 23px;
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 5px;
  background: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: background-color 0.1s linear, outline 0.1s linear, -webkit-transform 0.1s linear;
  transition: background-color 0.1s linear, transform 0.1s linear,
    outline 0.1s linear;
  transition: background-color 0.1s linear, transform 0.1s linear,
    outline 0.1s linear, -webkit-transform 0.1s linear;
}

.modal__btn:hover,
.modal__btn:focus-visible {
  background-color: #ebecf0;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.modal__btn:focus-visible {
  outline: 2px solid #0067a3;
  outline-offset: 2px;
}

.modal__btn:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

@media (max-width: 500px) {
  .modal__wrap {
    width: 70%;
  }

  .modal__text {
    width: 95%;
  }
}

.footer {
  display: flex;
  background-color: #0067A3;
  padding: 0 20px;
}

.tasks-active {
  margin-right: 36px;
}

.tasks-finished {

}

.tasks-creator {
  margin-left: auto;
}

@media (max-width: 500px) {
  .footer {
    display: none;
  }
}
